import React, { useEffect, useRef } from 'react';
import {
    ArrowRight, Sparkles, Crown, ShieldCheck,
    FileText, Zap, MousePointer2, ChevronRight
} from 'lucide-react';

const LandingPage = ({ onGetStarted, user, onLogin }) => {
    const containerRef = useRef(null);

    const [particles, setParticles] = React.useState([]);

    useEffect(() => {
        const handleMouseMove = (e) => {
            if (!containerRef.current) return;
            const { clientX, clientY } = e;
            const { innerWidth, innerHeight } = window;
            const x = (clientX / innerWidth - 0.5) * 12;
            const y = (clientY / innerHeight - 0.5) * 12;
            containerRef.current.style.setProperty('--mouse-x', `${x}px`);
            containerRef.current.style.setProperty('--mouse-y', `${y}px`);
        };

        // Initialize particles only on client
        setParticles(Array.from({ length: 30 }).map((_, i) => ({
            id: i,
            width: Math.random() * 2 + 1 + 'px',
            height: Math.random() * 2 + 1 + 'px',
            top: Math.random() * 100 + '%',
            left: Math.random() * 100 + '%',
            animationDuration: Math.random() * 10 + 10 + 's',
            animationDelay: Math.random() * -10 + 's',
            opacity: Math.random() * 0.3 + 0.1
        })));

        window.addEventListener('mousemove', handleMouseMove);
        return () => window.removeEventListener('mousemove', handleMouseMove);
    }, []);

    return (
        <div className="min-h-screen bg-[#02040a] text-white overflow-x-hidden font-sans selection:bg-brand-blue/30 selection:text-brand-blue">

            {/* AMBIENT LIGHTING ENVIRONMENT */}
            <div className="fixed inset-0 z-0 pointer-events-none">
                {/* Dynamic Brand Glows */}
                <div className="absolute top-0 left-1/4 w-[60%] h-[60%] bg-[radial-gradient(circle_at_50%_0%,rgba(37,99,235,0.08)_0%,transparent_70%)] blur-[100px]"></div>
                <div className="absolute bottom-0 right-1/4 w-[60%] h-[60%] bg-[radial-gradient(circle_at_50%_100%,rgba(245,158,11,0.05)_0%,transparent_70%)] blur-[100px]"></div>

                {/* Center Atmosphere */}
                <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full h-full bg-[radial-gradient(circle_at_50%_50%,rgba(37,99,235,0.03)_0%,transparent_50%)]"></div>

                {/* Architectural Accents */}
                <div className="absolute top-0 left-0 w-full h-[1px] bg-gradient-to-r from-transparent via-brand-blue/20 to-transparent"></div>
                <div className="absolute bottom-0 left-0 w-full h-[1px] bg-gradient-to-r from-transparent via-brand-gold/10 to-transparent"></div>

                {/* Floating Essence (Blue & Gold) */}
                <div className="absolute inset-0">
                    {particles.map((p) => (
                        <div
                            key={p.id}
                            className={`absolute rounded-full animate-float ${p.id % 2 === 0 ? 'bg-brand-blue/30' : 'bg-brand-gold/30'}`}
                            style={{
                                width: p.width,
                                height: p.height,
                                top: p.top,
                                left: p.left,
                                animationDuration: p.animationDuration,
                                animationDelay: p.animationDelay,
                                opacity: p.opacity
                            }}
                        />
                    ))}
                </div>

                {/* Subtle Luxury Texture */}
                <div className="absolute inset-0 opacity-[0.02] bg-[url('https://www.transparenttextures.com/patterns/carbon-fibre.png')]"></div>
            </div>

            {/* PROFESSIONAL NAVBAR */}
            <nav className="fixed top-0 left-0 w-full z-50 px-8 py-6 flex justify-between items-center backdrop-blur-2xl bg-[#02040a]/60 border-b border-white/5">
                <div className="flex items-center gap-4 group cursor-pointer">
                    <div className="relative w-11 h-11 bg-gradient-to-br from-brand-blue to-brand-blue-deep rounded-xl flex items-center justify-center transform group-hover:rotate-[15deg] transition-all duration-700 shadow-[0_0_30px_rgba(37,99,235,0.3)]">
                        <Crown className="w-5 h-5 text-white" />
                        <div className="absolute -inset-1 bg-brand-gold/20 rounded-xl blur-lg opacity-0 group-hover:opacity-100 transition-opacity"></div>
                    </div>
                    <div className="flex flex-col">
                        <span className="text-2xl font-serif italic tracking-tighter leading-none text-white">PaperPop</span>
                        <span className="text-[7px] font-black uppercase tracking-[0.5em] text-brand-gold/80 mt-1">Imena Studio</span>
                    </div>
                </div>

                <div className="hidden md:flex items-center gap-14">
                    {['Templates', 'Collections', 'Pricing'].map((item) => (
                        <a key={item} href="#" className="text-[10px] font-black uppercase tracking-[0.3em] text-neutral-500 hover:text-brand-blue transition-all duration-300 relative group">
                            {item}
                            <span className="absolute -bottom-2 left-0 w-0 h-[1px] bg-brand-blue transition-all duration-300 group-hover:w-full"></span>
                        </a>
                    ))}
                </div>

                <div className="flex items-center gap-8">
                    <button onClick={onLogin} className="text-[10px] font-black uppercase tracking-[0.3em] text-neutral-500 hover:text-white transition-all">Log In</button>
                    <button
                        onClick={onGetStarted}
                        className="px-8 py-3.5 rounded-full bg-white text-black text-[10px] font-black uppercase tracking-[0.25em] hover:bg-brand-blue hover:text-white transition-all duration-700 active:scale-95 shadow-[0_15px_30px_rgba(0,0,0,0.3)] hover:shadow-[0_15px_30px_rgba(37,99,235,0.4)]"
                    >
                        Start Designing
                    </button>
                </div>
            </nav>

            {/* MAIN STAGE */}
            <main className="relative z-10 min-h-screen flex flex-col items-center justify-center pt-24">

                <div
                    ref={containerRef}
                    className="max-w-7xl w-full flex flex-col items-center text-center transition-transform duration-700 ease-out"
                    style={{ transform: 'translate(var(--mouse-x, 0px), var(--mouse-y, 0px))' }}
                >
                    {/* Premiere Badge */}
                    <div className="inline-flex items-center gap-4 px-6 py-2.5 rounded-full border border-white/10 bg-white/5 backdrop-blur-3xl mb-16 animate-fade-in shadow-2xl">
                        <div className="w-2 h-2 rounded-full bg-brand-blue animate-pulse glow-blue"></div>
                        <span className="text-[9px] font-black text-white/80 tracking-[0.5em] uppercase">Private Creative Studio</span>
                    </div>

                    {/* Logo Focal Point */}
                    <div className="relative mb-12 group animate-scale-up">
                        <div className="absolute -inset-20 bg-brand-blue/20 rounded-full blur-[120px] opacity-0 group-hover:opacity-100 transition-opacity duration-[1.5s]"></div>
                        <div className="absolute -inset-10 bg-brand-gold/10 rounded-full blur-[80px] opacity-0 group-hover:opacity-100 transition-opacity duration-[2s] delay-300"></div>
                        <img
                            src="/imena-logo.svg"
                            alt="Imena"
                            className="h-40 md:h-60 object-contain filter drop-shadow-[0_30px_60px_rgba(0,0,0,0.8)] relative z-10 transition-transform duration-1000 group-hover:scale-105"
                        />
                    </div>

                    {/* High-End Heading */}
                    <header className="mb-14 animate-fade-in-up">
                        <h1 className="relative font-serif text-7xl md:text-[10rem] tracking-tighter leading-[0.8] mb-8">
                            <span className="block text-white/90 italic mb-2">Artisan</span>
                            <span className="block font-black text-transparent bg-clip-text bg-gradient-to-r from-white via-brand-blue to-brand-blue-deep drop-shadow-[0_0_30px_rgba(37,99,235,0.3)]">
                                Stationery.
                            </span>
                        </h1>
                        <p className="max-w-3xl mx-auto text-neutral-500 text-xs md:text-sm font-bold uppercase tracking-[0.5em] leading-relaxed">
                            Crafting elite family invitations <br />
                            <span className="text-white/70 italic lowercase font-serif normal-case tracking-normal text-xl mt-4 block">for the most significant occasions of your life.</span>
                        </p>
                    </header>

                    {/* Elite Action Bar */}
                    <div className="flex flex-col md:flex-row items-center gap-12 mt-16 animate-fade-in-up animation-delay-300">
                        <button
                            onClick={onGetStarted}
                            className="group relative px-16 py-7 bg-brand-blue rounded-2xl flex items-center gap-5 shadow-[0_25px_50px_-12px_rgba(37,99,235,0.4)] hover:shadow-[0_40px_80px_-12px_rgba(37,99,235,0.6)] transition-all duration-700 hover:-translate-y-2 active:translate-y-0 overflow-hidden"
                        >
                            <div className="absolute inset-0 bg-white/20 translate-y-full group-hover:translate-y-0 transition-transform duration-700 ease-[cubic-bezier(0.19,1,0.22,1)]" />
                            <span className="relative z-10 text-white text-[12px] font-black uppercase tracking-[0.4em]">Initialize Studio</span>
                            <ArrowRight className="relative z-10 w-5 h-5 text-white group-hover:translate-x-3 transition-transform duration-700" />
                        </button>

                        <div className="flex items-center gap-6 px-8 py-5 bg-white/5 backdrop-blur-3xl rounded-3xl border border-white/10 shadow-2xl">
                            <div className="flex -space-x-4">
                                {[10, 20, 30].map(i => (
                                    <div key={i} className="w-10 h-10 rounded-full border-2 border-[#02040a] bg-neutral-900 overflow-hidden shadow-xl transform hover:scale-110 hover:z-10 transition-all">
                                        <img src={`https://i.pravatar.cc/100?u=${i}`} className="w-full h-full object-cover grayscale" />
                                    </div>
                                ))}
                            </div>
                            <div className="h-8 w-[1px] bg-white/10"></div>
                            <div className="text-left">
                                <span className="block text-[10px] font-black text-white/90 uppercase tracking-[0.2em] leading-none mb-1.5">Trusted By</span>
                                <span className="block text-[9px] font-bold text-brand-blue uppercase tracking-[0.1em] leading-none">Global Creators</span>
                            </div>
                        </div>
                    </div>

                    {/* Refined Feature Strip */}
                    <div className="mt-40 grid grid-cols-1 md:grid-cols-3 gap-24 w-full max-w-6xl opacity-60 hover:opacity-100 transition-all duration-1000 animate-fade-in animation-delay-600">
                        <div className="flex flex-col items-center gap-6 group cursor-default">
                            <div className="w-14 h-14 rounded-2xl bg-brand-blue/10 flex items-center justify-center border border-brand-blue/20 group-hover:bg-brand-blue/20 group-hover:border-brand-blue/40 transition-all duration-500">
                                <ShieldCheck className="w-7 h-7 text-brand-blue" />
                            </div>
                            <h3 className="text-[11px] font-black uppercase tracking-[0.5em] text-white">Guaranteed Quality</h3>
                        </div>
                        <div className="flex flex-col items-center gap-6 group cursor-default">
                            <div className="w-14 h-14 rounded-2xl bg-brand-gold/10 flex items-center justify-center border border-brand-gold/20 group-hover:bg-brand-gold/20 group-hover:border-brand-gold/40 transition-all duration-500">
                                <Zap className="w-7 h-7 text-brand-gold" />
                            </div>
                            <h3 className="text-[11px] font-black uppercase tracking-[0.5em] text-white">Instant Generation</h3>
                        </div>
                        <div className="flex flex-col items-center gap-6 group cursor-default">
                            <div className="w-14 h-14 rounded-2xl bg-brand-blue/10 flex items-center justify-center border border-brand-blue/20 group-hover:bg-brand-blue/20 group-hover:border-brand-blue/40 transition-all duration-500">
                                <FileText className="w-7 h-7 text-brand-blue" />
                            </div>
                            <h3 className="text-[11px] font-black uppercase tracking-[0.5em] text-white">Professional PDF</h3>
                        </div>
                    </div>
                </div>

                {/* Vertical Scroll Indicator */}
                <div className="mt-40 pb-20 flex flex-col items-center gap-10 opacity-30">
                    <div className="h-28 w-[1px] bg-gradient-to-b from-transparent via-brand-blue to-transparent"></div>
                    <span className="text-[10px] font-black uppercase tracking-[1.2rem] mr-[-1.2rem] text-brand-blue">Explore</span>
                </div>
            </main>

            {/* MINIMALIST FOOTER CORNERS */}
            <div className="fixed bottom-12 left-12 flex flex-col gap-3 z-50 pointer-events-none opacity-40">
                <span className="text-[10px] font-black uppercase tracking-widest text-white">© 2024</span>
                <div className="h-[1px] w-12 bg-brand-blue"></div>
                <span className="text-[10px] font-black uppercase tracking-[0.4em] text-brand-blue">Imena Group</span>
            </div>

            <div className="fixed bottom-12 right-12 flex flex-col items-end gap-3 z-50 pointer-events-none opacity-40">
                <div className="flex items-center gap-4">
                    <span className="text-[10px] font-black uppercase tracking-[0.5em] text-brand-gold">Designed for Excellence</span>
                    <MousePointer2 className="w-4 h-4 text-brand-gold animate-bounce" />
                </div>
                <div className="h-[1px] w-12 bg-brand-gold"></div>
            </div>

        </div>
    );
};

export default LandingPage;
